<template>
  <section class="container mx-auto" v-if="!isLoading">
    <section class="w-80 mx-auto flex-center flex-column mb-6">
      <h1 class="d-block mb-3">修改密碼</h1>
      <div class="input-group input-group-lg mb-3">
        <div class="input-group-prepend">
          <span class="input-group-text" id="inputGroup-sizing-lg">新密碼</span>
        </div>
        <input
          type="text"
          class="form-control"
          aria-label="Large"
          aria-describedby="inputGroup-sizing-sm"
          v-model="newPassword"
        >
      </div>
      <div class="input-group input-group-lg mb-3">
        <div class="input-group-prepend">
          <span class="input-group-text" id="inputGroup-sizing-lg">確認新密碼</span>
        </div>
        <input
          type="text"
          class="form-control"
          aria-label="Large"
          aria-describedby="inputGroup-sizing-sm"
          v-model="confirmNewPassword"
        >
      </div>
      <div v-if="isAlertShow" class="text-danger mb-3">兩者密碼不同喔'</div>
      <button class="d-block btn btn-primary" @click.prevent="modifyPassword">確認修改</button>
    </section>
    <section class="w-80 mx-auto pt-6">
      <h1 class="d-block text-center">訂單列表</h1>
      <ul v-if="orderData.length !== 0 ">
        <li v-for="(order,index) in orderData" :key="index" class="border my-3 p-3">
          <h3>購買商品</h3> 
          <div v-for="(product,index) in order.cartData" :key="index" class="d-flex align-items-center my-3">
            <img :src="product.imgUrl" class="product_img"><pre class="ml-3 h4">{{product.fullId}}   {{product.title}}-{{product.colorObject.colorName}}-{{product.sizeObject.sizeName}}   x{{product.qty}}件   總價 {{product.totalPrice}}</pre>
          </div>
         <h3 class="mb-3">收貨資訊</h3> 
          <h4 class="ml-3">收貨人{{order.receiverData.name}}
            連絡電話{{order.receiverData.phone}}
            收貨地址{{order.receiverData.address}}</h4>
        </li>
      </ul>
      <div v-else class="text-center"><h2 >你還沒下訂任何商品喔</h2></div>
    </section>
  </section>
</template>
<script>
import AxiosClass from "@/config/axiosClass.js";
export default {
  name: "user",
  data() {
    return {
      orderData: [],
      newPassword: "",
      confirmNewPassword: "",
      alert: {
        text: "",
        class: ""
      },
      isAlertShow: false
    };
  },
  computed: {
    isLoading() {
      return this.$store.state.isLoading;
    },
    isPasswordSame() {
      if (this.newPassword === this.confirmNewPassword) {
        return true;
      } else {
        return false;
      }
    }
  },
  methods: {
    ensureLogin() {
      const self = this;
      const ensureLogin = new AxiosClass("ensure_login", "get", {}, true);
      ensureLogin.doAxios(
        self,
        function() {},
        function(res) {
          self.$router.push("/login");
        }
      );
    },
    getOrder() {
      const self = this;
      const getOrder = new AxiosClass("user/order", "get", {}, true);
      getOrder.doAxios(
        self,
        function(res) {
          console.log(res);
          self.orderData = res.data.data;
        },
        function(res) {}
      );
    },
    modifyPassword() {
      if (!this.isPasswordSame) {
        this.isAlertShow = true;
        return;
      } else {
        const self = this;
        const modifyPassword = new AxiosClass(
          "modify",
          "post",
          this.newPassword,
          true
        );
        modifyPassword.doAxios(self, function(res) {}, function(res) {});
      }
    }
  },
  created() {
    this.ensureLogin();
    this.getOrder();
    console.log(File)
  }
};
</script>
<style lang="scss" scoped>
.product_img {
  height: 50px;
}
</style>
